<template>
  <div class="recordList-box">
    <div class="list-item" v-for="(item, index) in listData" :key='index'>
      <div class="left" @click="handlerLeft(item.rcordId)">
        <div class="item-title">
          {{item.recordTitle}}
        </div>
        <div class="left-bottom">
          {{item.recordTime}}
        </div>
      </div>
      <div class="right">
        <div class="main">
          <div class="item-state">
            {{item.recordState}}
          </div>
          <div class="item-operate" v-if="item.billStatusIndex == 10 ? true : false">
            <span @click="handlerBtn(item.rcordId)">取消</span>
          </div>
        </div>
        <div class="item-icon" @click="handlerIcon(item.rcordId)"></div>
        
      </div>
    </div>
  </div>
  
</template>


<script>
  export default {
    name: 'recordList',
    data() {
      return {}
    },
    props: {
      listData: {
        type: Array,
        default: () => {
          return [];
        }
      }
    },
    components: {},
    methods: {
      handlerLeft(rcordId) {
        this.$emit('on-handlerLeft',rcordId);
      },
      handlerIcon(rcordId) {
        this.$emit('on-handlerIcon',rcordId);
      },
      handlerBtn(rcordId) {
        this.$emit('on-handlerBtn',rcordId);
      }
    }
  }
</script>

<style scoped lang="scss">
  .list-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 .1rem;
    font-size: .16rem;
    background-color: #fff;
    height: .69rem;
    border-bottom: 1px solid #dddddd;

    .left {
      width: 80%;
      .left-bottom {
        font-size: .14rem;
        color: #8B8B8D;
        margin-top: .07rem;
      }
    }

    .right {
      display: flex;
      align-items: center;
      font-size: .14rem;

      .main {
        text-align: right;
        .item-operate {
          padding-top: .1rem;
          color: #8B8B8D;
        }

      }

      .item-icon {
        display: inline-block;
        margin-left: 0.19rem;
        width: .1rem;
        height: .1rem;
        background: url('~@/assets/icon-next.png') no-repeat;
        background-size: 100%;
        transform: rotate(-90deg);
        opacity: 0.4;
      }
    }

  }

</style>



