<template>
  <div class="signature">
    <span class="label-name">手写签名：</span>
    <img @click="clear" class="del-content" src="../assets/signatureDel-icon.png" v-if="!disabled">
    <vueSignature ref="signature" :h="'2.5rem'" :disabled="disabled"></vueSignature>
  </div>
</template>

<script>
  export default {
    name: 'signature',
    data() {
      return {
        disabled: false
      }
    },
    props: {
    
    },
    components: {

    },
    methods: {
      save(){
        let png = this.$refs.signature.save();
        let jpeg = this.$refs.signature.save('image/jpeg');
        let svg = this.$refs.signature.save('image/svg+xml');
        return png;
      },
      clear(){
        this.$refs.signature.clear();
      },
      fromDataURL(imgSrc) {
        this.$refs.signature.fromDataURL(imgSrc.toString());
      },
    }
  }
</script>

<style scoped lang="scss">
  .signature {
    position: relative;
    padding: .15rem;
    margin-top: .1rem;
    font-size: .16rem;
    background-color: #fff;

    .label-name {
      position: absolute;
      z-index: 1;
      left: .15rem;
      top: .15rem;
    }

    .del-content {
      position: absolute;
      z-index: 1;
      right: .15rem;
      top: .2rem;
    }
  }
</style>



