import request from '@/utils/request'

export function getBatchNumberList() { //获取批次列表
  return request({
    url: '/api/batchRecord/batchListList',
    method: 'get',
  })
}

export function feedGetBatchNumberList() { // 获取领料批次列表
  return request({
    url: '/api/batchRecord/batchListList?flag=3',
    method: 'get',
  })
}

export function getDriverList() { //获取司机列表
  return request({
    url: '/api/material/getDriverList',
    method: 'get',
    params: {
      pageNumber: 1,
      pageSize: 30
    }
  })
}

export function getFeedList(batchId) { //获取饲料列表
  return request({
    url: `/api/material/getFeedList?batchId=${batchId}`,
    method: 'get',
  })
}


export function saveFeedApply(data) { //保存领料申请
  return request({
    url: `/api/material/saveFeedApply`,
    method: 'post',
    data
  })
}

export function getfeedRecordList(params) { //获取领料列表
  return request({
    url: `/api/material/feedList`,
    method: 'get',
    params
  })
}

export function canclefeedApply(rcordId) { // 取消领料申请
  return request({
    url: `/api/material/canclefeedApply/?rcordId=${rcordId}`,
    method: 'get',
  })
}

export function viewInfoByIdFeed(rcordId) { //查看实际领料明细详情
  return request({
    url: `/api/material/viewInfoById?rcordId=${rcordId}`,
    method: 'get',
  })
}

export function saveSignerFeedApply({rcordId, fileEntry}) { // 领料签名
  return request({
    url: `/api/material/signerFeedApply`,
    method: 'post',
    data: fileEntry,
    params: {
      rcordId
    }
  })
}

export function balanceBatchNumberList() { //获取结算批次列表
  return request({
    url: '/api/batchRecord/batchListList?flag=2',
    method: 'get',
  })
}

export function initInfoByBatchId (batchId) {  //初始化结算申请
  return request({
    url: `/api/balanceRecord/initInfoByBatchId?batchId=${batchId}`,
    method: 'get'
  })
}

export function saveBalanceRecord (data) {  //保持结算记录
  return request({
    url: `/api/balanceRecord/apply`,
    method: 'post',
    data
  })
}

export function getBalanceRecordList(params) { //获取结算列表
  return request({
    url: `/api/balanceRecord/list`,
    method: 'get',
    params
  })
}

export function cancleBalanceApply(rcordId) { // 取消结算申请
  return request({
    url: `/api/balanceRecord/cancleApply/?rcordId=${rcordId}`,
    method: 'get',
  })
}

export function balanceDeatils(rcordId) { // 结算申请详情
  return request({
    url: `/api/balanceRecord/viewInfoById/?rcordId=${rcordId}`,
    method: 'get',
  })
}

export function saveBalanceRecordsigner({rcordId, fileEntry}) { // 结算签名
  return request({
    url: `/api/balanceRecord/signer`,
    method: 'post',
    data: fileEntry,
    params: {
      rcordId
    }
  })
}

export function queryEquipmentApplyInitData () {   //初始化设备申请
  return request({
    url: '/api/material/queryEquipmentApplyInitData',
    method: 'get'
  })
}

export function queryEntrustList () {   //获取委托人信息
  return request({
    url: '/api/material/queryEntrustList',
    method: 'get'
  })
}

export function searchEquimp (keyword) {   //获取设备名称
  return request({
    url: `/api/material/searchEquimp?keyword=${keyword}`,
    method: 'get'
  })
}

export function saveEquipmentApply (data) {   //保存设备申请
  return request({
    url: '/api/material/saveEquipmentApply',
    method: 'post',
    data
  })
}

export function queryEquipmentApplyRecordList (params) {   //设备申请记录列表
  return request({
    url: '/api/material/queryEquipmentApplyRecordList',
    method: 'get',
    params
  })
}

export function deleteEquipmentApplyInfoById(rcordId) { // 取消设备设备申请
  return request({
    url: `/api/material/deleteEquipmentApplyInfoById/?id=${rcordId}`,
    method: 'get',
  })
}

export function queryEquipmentApplyInfoById(rcordId) { // 设备申请详情
  return request({
    url: `/api/material/queryEquipmentApplyInfoById/?rcordId=${rcordId}`,
    method: 'get',
  })
}

export function saveSignerEquipApply({rcordId, fileEntry}) { // 设备领用签名
  return request({
    url: `/api/material/signerEquipApply`,
    method: 'post',
    data: fileEntry,
    params: {
      rcordId
    }
  })
}

export function saveDrug (data) { //领药申请提交
  return request({
    url: '/api/material/saveDrugApply',
    method: 'post',
    data
  })
}
export function getDrugList (params) { //领药申请记录列表
  return request({
    url: '/api/material/queryDrugApplyRecordList',
    method: 'get',
    params
  })
}

export function deleteDrug (data) { //删除领药记录
  return request({
    url: '/api/material/deleteDrugApplyInfoById',
    method: 'get',
    params:{
      id:data
    }
  })
}

export function queryDrugApplyInfoById (rcordId) { //领药详情
  return request({
    url: `/api/material/queryDrugApplyInfoById?rcordId=${rcordId}`,
    method: 'get'
  })
}

export function saveSignerDrugApply({rcordId, fileEntry}) { // 领药签名
  return request({
    url: `/api/material/signerDrugApply`,
    method: 'post',
    data: fileEntry,
    params: {
      rcordId
    }
  })
}





