import request from '@/utils/request'

export function addApply(data) { // 新增外出申请
  return request({
    url: '/api/personGoOut/addApply',
    method: 'post',
    data
  })
}

export function getBiologySafeList(data) { // 生物安全列表
  return request({
    url: '/api/personGoOut/getBiologySafeList',
    method: 'post',
    data
  })
}

export function getApplyRecord(data) { // 生物安全记录详情
  return request({
    url: '/api/personGoOut/getApplyById',
    method: 'post',
    data
  })
}

export function getApplyStepRecord() { // 生物安全记录步骤详情
  return request({
    url: '/api/personGoOut/queryClearStep',
    method: 'get',
  })
}



