let subMitBtnMixins = {
  data: function () {
    return {
      isDisabledSubmit: false,
      myCustomStyle: {
        background: 'linear-gradient(90deg, rgba(248, 155, 49, 1) 0%, rgba(255, 106, 29, 1) 100%)',
        color: '#fff'
      },
      submitBtnText: '提交'
    }
  },
  watch: {
    'isDisabledSubmit': function (newValue, oldValue){
      if (newValue) {
        this.myCustomStyle = {
          background: "linear-gradient(90deg, rgba(128, 123, 120, 1) 0%, rgba(88, 78, 73, 1) 100%)",
          color: "#fff"
        }
      } else {
        this.myCustomStyle = {
          background: 'linear-gradient(90deg, rgba(248, 155, 49, 1) 0%, rgba(255, 106, 29, 1) 100%)',
          color: '#fff'
        }
      }
    }
  }
};

export default subMitBtnMixins;




