import {getParam} from './wxUtil'
export function formatDate(date, fmt) {
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
  }
  let o = {
    'M+': date.getMonth() + 1,
    'd+': date.getDate(),
    'h+': date.getHours(),
    'm+': date.getMinutes(),
    's+': date.getSeconds()
  }
  for (let k in o) {
    if (new RegExp(`(${k})`).test(fmt)) {
      let str = o[k] + ''
      fmt = fmt.replace(RegExp.$1, RegExp.$1.length === 1 ? str : padLeftZero(str))
    }
  }
  return fmt
}

function padLeftZero(str) {
  return ('00' + str).substr(str.length)
}

// 将例如‘2017-08-10’转化成时间戳
export function toTransformTime(time) {
  let date = new Date(time.replace(/\-/g,'/'));
  return date.getTime();
}

//得到当前时间的前n天
export function getStartDate (n, fmt = 'yyyy-MM-dd') {
  let curTime = new Date().getTime();
  let startDate = curTime - (n * 3600 * 24 * 1000);
  if (!fmt) {
    return startDate;  //返回时间戳格式
  } else {
    return formatDate(new Date(startDate),fmt); //返回格式化的时间
  }
}

//得到当前时间的后n天
export function getEndDate (n, fmt = 'yyyy-MM-dd') {
  let curTime = new Date().getTime();
  let endDate = curTime + (n * 3600 * 24 * 1000);
  if (!fmt) {
    return endDate;  //返回时间戳格式
  } else {
    return formatDate(new Date(endDate),fmt); //返回格式化的时间
  }
}

// 获取指定日期的后一天（比如现在是“2014-06-23”，我想得到“2014-06-24”）
export function getNextDay(d){
  d = new Date(d);
  d = +d + 1000*60*60*24;
  d = new Date(d);
  return d;
}

//获取当前的后n个小时（例如现在是“2019-07-18 09:24，我想得到”2019-07-18 12:24）
export function getEndHour(n,fmt="yyyy-MM-dd hh:mm") {
  let curTime = new Date().getTime();
  let endDate = curTime + (n * 3600  * 1000);
  if (!fmt) {
    return endDate;  //返回时间戳格式
  } else {
    return formatDate(new Date(endDate),fmt); //返回格式化的时间
  }
}

//获取当前的后n个小时（例如现在是“2019-07-18 09:24，我想得到”2019-07-18 12:24）
export function getDefineEndHour(date,n,fmt="yyyy-MM-dd hh:mm") {
  let endDate = date + (n * 3600  * 1000);
  if (!fmt) {
    return endDate;  //返回时间戳格式
  } else {
    return formatDate(new Date(endDate),fmt); //返回格式化的时间
  }
}
export function  nowDate(){ //获取当前时间
  let now = new Date();
  let k = {
    'y':now.getFullYear(),
    'mon':function () {
      if(now.getMonth()+1<10){
        return `0${now.getMonth()+1}`
      }else{
       return now.getMonth()+1
      }
    },
    'd':function (){
      if(now.getDate()<10){
        return `0+${now.getDate()}`
      }else{
        return now.getDate()
      }
    },
    'h':function () {
      if(now.getHours()<10){
        return `0${now.getHours()}`
      }else {
        return now.getHours()
      }
    },
    'm':function () {
      if(now.getMinutes()<10){
        return `0${now.getMinutes()}`
      }else{
        return now.getMinutes()
      }
    },
    's':function () {
      if(now.getSeconds() <10){
        return `0${now.getSeconds()}`
      }else{
        return now.getSeconds()
      }
    }
  }

  return `${k.y}-${k.mon()}-${k.d()} ${k.h()}:${k.m()}:${k.s()}`
}
export function  getId (name) { //处理特殊符号，转义， +变空号
  const url = window.location.href
  return getParam(url, name).replace('%20', '+')
}



