import request from '@/utils/request'

export function askQuestions(data) { //问题反馈提交
  return request({
    url: '/api/questionReport/saveQuestionReport',
    method: 'post',
    params:{
      details:data
    }
  })
}

export function queryQuestionList() { //问题反馈列表
  return request({
    url: '/api/questionReport/queryQuestionList',
    method: 'get'
  })
}

export function queryQuestionInfoById(data) { //查询问题详情
  return request({
    url: '/api/questionReport/queryQuestionInfoById',
    method: 'get',
    params:{
      id:data
    }
  })
}

export function deleteQuestionInfoById(data) { //删除问题
  return request({
    url: '/api/questionReport/deleteQuestionInfoById',
    method: 'get',
    params:{
      id:data
    }
  })
}




