<template>
  <div>
    <!--选择开始时间-->
    <SegmentTimePicker
      ref="filterPickerBefore"
      type="date"
      v-model="filterDateBefore"
      cancelText="取消"
      confirmText="下一步"
      titleText="开始时间"
      @confirm="filterDateConfirmBefore">
    </SegmentTimePicker>
    
    <!--选择结束时间-->
    <SegmentTimePicker
      ref="filterPickerAfter"
      type="date"
      v-model="filterDateAfter"
      :startDate="startDateAfter"
      cancelText="上一步"
      confirmText="确定"
      titleText="结束时间"
      @confirm="filterDateConfirmAfter"
      @cancel="filterDateCloseAfter">
    </SegmentTimePicker>
  
  </div>
</template>

<script>
  import {formatDate, getStartDate, getNextDay} from '@/utils/tool'
  import SegmentTimePicker from '@/components/SegmentTimePicker';
  export default {
    name: 'filterTime',
    data() {
      return {
        filterDateBefore: new Date(),
        filterDateAfter: new Date(),
        // endDateBefore: new Date(),
        // endDateAfter: new Date(),
        startDateAfter: new Date(),
        // startDateBefore: new Date(getStartDate(365)), // 前3年
      }
    },
    props: {
    
    },
    components: {
      SegmentTimePicker
    },
    methods: {
      //暴露对外的控制显示
      selectFilterDate() {
        this.$refs.filterPickerBefore.open();
      },
      //下一步
      filterDateConfirmBefore (val) {
        this.$refs.filterPickerAfter.open();
        
        this.$nextTick(() => {
          this.filterDateBefore == '' ? this.filterDateBefore = new Date() : null;
          
          this.startDateAfter = this.filterDateBefore;
          
        });
        
      },
      //上一步
      filterDateCloseAfter () {
        this.$refs.filterPickerBefore.open();
      },
      //确定
      filterDateConfirmAfter (val) {
        this.$emit('on-filter-search', {
          starttime: this.filterDateBefore ? formatDate(this.filterDateBefore, 'yyyy-MM-dd') : '',
          endtime: this.filterDateAfter ? formatDate(this.filterDateAfter, 'yyyy-MM-dd') : ''
        });
      }
    }
  }
</script>

<style scoped lang="scss">
  .my-prop {
    .mint-popup {
      width: 100%;
      height: 25%;
    }
  }
</style>



