<template>
  <div class="submit-bth" :style="customStyle">{{btnText}}</div>
</template>

<script>
  export default {
    name: 'submitBtn',
    data() {
      return {

      }
    },
    props: {
      customStyle: {
        type: Object,
        default: function () {
          return  {
            background: 'linear-gradient(90deg, rgba(248, 155, 49, 1) 0%, rgba(255, 106, 29, 1) 100%)',
            color: '#fff'
          }
        }
      },
      btnText: {
        type: [String, Number],
        default: '提交'
      }
    },
    methods: {

    }
  }
</script>

<style scoped lang="scss">
  .submit-bth {
    font-size: .16rem;
    margin: .2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 1;
    border-radius: .3rem;
    height: .4rem;
  }
</style>



