import request from '@/utils/request'

export function getFeedPersonInfo () {
  return request({
    url: '/api/myInfo/list',
    method: 'get'
  })
}

export function putApplyOpenAccount (data) {
  return request({
    url: '/api/register/save',
    method: 'post',
    data
  })
}



