import request from '@/utils/request'

export function queryBillRecordList (data) { //对账单查询列表
  return request({
    url: '/api/bill/queryBillRecordList',
    method: 'get',
    params:{
      period:data
    }
  })
}

export function queryBillRecordById (params) {  //对账单记录查询
  return request({
    url: '/api/bill/queryBillRecordById',
    method: 'get',
    params
  })
}

export function yuEQuery(params) {  //保障金余额查询
  return request({
    url: '/api/yuEQuery/query',
    method: 'get',
    params
  })
}

export function getBatchListList () {   // 获取批次列表下拉框
  return request({
    url: '/api/batchRecord/batchListList',
    method: 'get'
  })
}

export function getEttlementBatchListList () {   // 获取结算记录列表下拉框
  return request({
    url: `/api/batchRecord/batchListList?flag=1`,
    method: 'get'
  })
}

export function recipientsRecordsGetBatchNumberList() { // 获取领用记录批次列表
  return request({
    url: '/api/batchRecord/batchListList?flag=3',
    method: 'get',
  })
}

export function batchRecordsGetBatchNumberList() { // 获取批次档案批次列表
  return request({
    url: '/api/batchRecord/batchListList?flag=3',
    method: 'get',
  })
}

export function batchRecordInfo(batchid) {   // 批次详情
  return request({
    url: `/api/batchRecord/list?batchid=${batchid}`,
    method: 'get'
  })
}

export function receivedRecord({type,batchId,pageNumber,pageSize,state}) {//获取领料、设备、领药记录
  return request({
    url: '/api/receiveRecord/receivedRecord',
    method: 'get',
    params: {
      type,
      batchId,
      pageNumber,
      pageSize,
      state
    }
  })
}

export function getfeedRecordList(params) { //获取领料记录列表
  return request({
    url: `/api/material/feedList`,
    method: 'get',
    params
  })
}

export function queryEquipmentApplyRecordList (params) {   //设备申请记录列表
  return request({
    url: '/api/material/queryEquipmentApplyRecordList',
    method: 'get',
    params
  })
}

export function queryDrugApplyRecordList(params) { //领药申请记录列表
  return request({
    url: '/api/material/queryDrugApplyRecordList',
    method: 'get',
    params
  })
}

export function getBalanceRecord(batchId) { //领药申请记录列表
  return request({
    url: `/api/balanceRecord/viewInfoById?batchId=${batchId}`,
    method: 'get',
  })
}



