import request from '@/utils/request'

export function getBatchNumberList() { //获取批次列表
  return request({
    url: '/api/batchRecord/batchListList',
    method: 'get',
  })
}

export function getPigwashList(batchId) { // 获取饲料列表
  return request({
    url: `/api/material/getFeedList?batchId=${batchId}`,
    method: 'get'
  })
}

export function saveFeedRecord(data) { // 增加饲喂记录
  return request({
    url: '/api/feedRecord/save',
    method: 'post',
    data
  })
}

export function getfeedRecordList(params) { //获取饲喂记录列表
  return request({
    url: '/api/feedRecord/list',
    method: 'get',
    params
  })
}

export function cancleFeedRecord(rcordId) { //取消饲喂记录列表
  return request({
    url: `/api/feedRecord/cancleById?rcordId=${rcordId}`,
    method: 'get'
  })
}

export function getfeedRecordDetails(rcordId) { //获取饲喂记录详情
  return request({
    url: `/api/feedRecord/queryInfoById?rcordId=${rcordId}`,
    method: 'get',
  })
}

export function saveDeathApply(data) { // 增加死亡记录
  return request({
    url: `/api/deathApply/save`,
    method: 'post',
    data
  })
}

export function getDeathApplyList(params) { // 获取死亡申报记录列表
  return request({
    url: `/api/deathApply/list`,
    method: 'get',
    params
  })
}

export function cancleDeathApply(rcordId) { // 取消死亡申报记录
  return request({
    url: ` /api/deathApply/cancleById/?rcordId=${rcordId}`,
    method: 'get',
  })
}

export function getDeathRecordDetails(rcordId) { // 获取死亡申报记录详情
  return request({
    url: `/api/deathApply/queryInfoById?rcordId=${rcordId}`,
    method: 'get',
  })
}

export function saveDeathApplySinger({rcordId, fileEntry}) { // 死亡申报签名
  return request({
    url: `/api/deathApply/singer`,
    method: 'post',
    data: fileEntry,
    params: {
      rcordId
    }
  })
}


export function saveToumiaoApply(data) { //投苗申请
  return request({
    url: 'api/toumiao/saveToumiaoApply',
    method: 'post',
    params: data
  })
}

export function queryTouMiaoRecordList(data) { //投苗记录列表
  return request({
    url: 'api/toumiao/queryTouMiaoRecordList',
    method: 'get',
    params: data
  })
}

export function queryTouMiaoInitData() { //初始化投苗数据
  return request({
    url: '/api/toumiao/queryTouMiaoInitData',
    method: 'get'
  })
}

export function queryInfoById(recordId) { //投苗记录详情
  return request({
    url: '/api/toumiao/queryInfoById',
    method: 'get',
    params: {
      recordId: recordId
    }
  })
}

export function signerTouMiao(data) { // 投苗签名
  return request({
    url: '/api/toumiao/signerTouMiao',
    method: 'post',
    data
  })
}

export function deleteCast(recordId) { //删除记录
  return request({
    url: '/api/toumiao/cancleTouMiao',
    method: 'get',
    params: {
      rcordId: recordId
    }
  })
}

export function getSalesList() { //销售通知列表
  return request({
    url: '/api/saleNotice/querySale',
    method: 'get',
  })
}

export function getSalesRecord(id) { //销售通知详情
  return request({
    url: '/api/saleNotice/viewSale',
    method: 'get',
    params: {
      id: id
    }
  })
}
export function getDieDay(birthDay,dieDay) { // 计算出生日龄
  return request({
    url: `/api/deathApply/initDieDay`,
    method: 'get',
    params: {
      birthday:birthDay,
      dieDate:dieDay
    }
  })
}




