import { Toast } from 'mint-ui'

const regConfig = {
  tel: {rule: /^[1][3,4,5,7,8,9][0-9]{9}$/, name: '手机号码', tipText: '请输入有效的手机号码'},
  IDCard: {rule: /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/, name: '身份证号码',tipText: '请输入有效的身份证号码'},
  ChineseName: {rule: /^[\u4E00-\u9FA5]{2,8}$/, name: '姓名',tipText: '请输入有效的中文名'},
  agreeAppointment: {name: '协议',tipText: '请勾选阅读内容'},
  platNumber: {rule: /^(([京津沪渝冀豫云辽黑湘皖鲁新苏浙赣鄂桂甘晋蒙陕吉闽贵粤青藏川宁琼使领][A-Z](([0-9]{5}[DF])|([DF]([A-HJ-NP-Z0-9])[0-9]{4})))|([京津沪渝冀豫云辽黑湘皖鲁新苏浙赣鄂桂甘晋蒙陕吉闽贵粤青藏川宁琼使领][A-Z][A-HJ-NP-Z0-9]{4}[A-HJ-NP-Z0-9挂学警港澳使领]))$/ ,
    name: '车牌号',tipText: '请输入有效的车牌号'}
};

// 限制输入框只能输入数字和小数
export function inputLimitNum (e, num) {
  if (!num) { //正整数
    return e.target.value.replace(/[^0-9]+/g,'');
  } else {
    let re =new RegExp("^\\d*(\\.?\\d{0,"+num+"})","g");
    return (e.target.value.match(re)[0]) || null;
  }

}

//表单提交校验
export function submitValidator(checkArr) {
  console.log(checkArr);
  for (let i = 0 ; i < checkArr.length; i++) {
    // 特殊的项，可以扩展
    if (checkArr[i].rule.includes('requiredChecked') && !checkArr[i].val) {
      Toast({
        message: `${regConfig[checkArr[i].type].tipText}`,
        duration: 1000
      });
      return false;
    }

    if (checkArr[i].rule.includes('required') &&!checkArr[i].val) {
      if (checkArr[i].type) {  //有类型的必填项
        Toast({
          message: `${regConfig[checkArr[i].type].name}不能为空`,
          duration: 1000
        });
      } else {  //无类型的必填项(不用传type，但是需要传‘getTipText’)
        Toast({
          message: `${checkArr[i].getTipText}不能为空`,
          duration: 1000
        });
      }

      return false;
    }

    //有校验规则的项
    if (checkArr[i].rule.includes('standard') &&
      !regConfig[checkArr[i].type].rule.test(checkArr[i].val)) {
      Toast({
        message: regConfig[checkArr[i].type].tipText,
        duration: 1000
      });
      return false;
    }

    if (i == (checkArr.length - 1)) {
      return true;
    }
  }
}

