<template>
  <header class="head">
    <div class="left ali-c" v-if="isBack && backPath !== '/'">
      <span class="line line1"></span>
      <span class="line line2"></span>
      <router-link :to="backPath"
                   tag="div" class="back-text ali-c">返回</router-link>
    </div>
  
    <div class="left ali-c" v-if="isBack && backPath == '/'" @click="back">
      <span class="line line1"></span>
      <span class="line line2"></span>
      <div class="back-text ali-c">返回</div>
    </div>
    
    <div class="title">
      <span @click="onHandlerTitlte">{{title}}</span>
    </div>
    
    <router-link v-if="subtitlePath !== '/'"
                 :to="subtitlePath"
                 tag="div" class="right ali-c f12">
      {{subtitle}}
    </router-link>
  </header>
</template>

<script>
export default {
  name: "Header",
  props: {
    isBack: {  //必填
      type: Boolean,
      default: false
    },
    backPath: {
      type: [String, Object],
      default: '/'
    },
    title: { //必填
      type: String,
      default: "标题"
    },
    subtitle: {
      type: String,
      default: '副标题'
    },
    subtitlePath: {
      type: [String, Object],
      default: '/'
    }
  },
  created() {
    console.log(this.backPath);
  },
  data() {
    return {};
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    onHandlerTitlte() {
      this.$emit('on-handler-title');
    }
  }
};
</script>

<style lang="scss" scoped>
.ali-c {
  display: flex;
  align-items: center;
}

.head {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  height: 0.42rem;
  background-color: #fff;
  border-bottom: 1px solid #ebebeb;
  text-align: center;
  font-size: 0.16rem;
  color: #1e2834;
  z-index: 999;
  .left-ico {
    padding-left: 0.14rem;
  }
  .left {
    width: 0.6rem;
    position: absolute;
    left: 0;
    top: 0;
    height: 0.42rem;
    .line {
      content: "";
      position: absolute;
      top: 50%;
      left: 0.06rem;
      width: 0.12rem;
      height: 1px;
      background-color: #1e2834;
    }
    .line1 {
      margin-top: -0.04rem;
      -moz-transform: rotate(-45deg);
      -webkit-transform: rotate(-45deg);
      transform: rotate(-45deg);
    }
    .line2 {
      margin-top: 0.04rem;
      -moz-transform: rotate(45deg);
      -webkit-transform: rotate(45deg);
      transform: rotate(45deg);
    }
    .back-text {
      font-size: 0.14rem;
      margin-left: 0.24rem;
      color: #1e2834;
    }
  }
  .title {
    width: 100%;
    height: 100%;
    font-size: 0.16rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .right {
    position: absolute;
    right: 0;
    top: 0;
    height: 0.42rem;
    padding: 0 0.12rem;
  }
}
</style>



