<template>
  <nav id="navBtm">
    <router-link class="nav-tab" tag="div" :class="{'active':currentTabName === 'business'}" to="business">
      <div class="item-nav">
        <i class="iconfont">&#xe621;</i>
        <div class="item-title">我的业务</div>
      </div>
    </router-link>
    <router-link class="nav-tab" tag="div" :class="{'active':currentTabName === 'finance'}" to="finance">
      <div class="item-nav">
        <i class="iconfont">&#xe622;</i>
        <div class="item-title">财务中心</div>
      </div>
    </router-link>
    <router-link class="nav-tab" tag="div" :class="{'active':currentTabName === 'personInfo'}" to="personInfo">
      <div class="item-nav">
        <i class="iconfont">&#xe613;</i>
        <div class="item-title">个人中心</div>
      </div>
    </router-link>
  </nav>
</template>

<script>
  export default {
    name: 'bottomNav',
    computed: {
      currentTabName() {
        return this.$route.name
      }
    }
  }
</script>

<style lang="scss">
  #navBtm {
    position: fixed;
    z-index: 999;
    bottom: 0;
    display: flex;
    border-top: 1px solid #ebebeb;
    height: 50px;
    width: 100%;
    
    & div.nav-tab {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      flex: 1;
      font-size: .14rem;
      color: #848484;
      transition: color .5s;
      background-color: #fff;
      
      & .iconfont {
        font-size: .2rem;
      }
      
      &.active {
        color: #F6911D;
      }
      
      .item-nav {
        width: 100%;
        text-align: center;
      }
    }
  }
</style>



